/* eslint-disable react/no-danger */
import { graphql } from 'gatsby'
import { SocialShare, Utterances } from '@/components'
import '@/styles/code.css'
import 'katex/dist/katex.min.css'

interface PostTemplateProps {
  data: {
    markdownRemark: {
      html: string
      frontmatter: {
        title: string
        date: string
        category_id: string
      }
    }
    site: {
      siteMetadata: {
        comment: {
          utterances: string
        }
        author: string
      }
    }
  }
}

const PostTemplate = ({ data }: PostTemplateProps) => {
  const post = data.markdownRemark
  const metaData = data.site.siteMetadata
  const { comment, author } = metaData
  const { title: postTitle, date } = post.frontmatter
  const { utterances } = comment

  return (
    <main className="max-w-[700px] mx-auto pt-16 pb-24 prose">
      {/* <Global styles={[codeStyle, notionStyle]} /> */}
      <h1>{postTitle}</h1>
      <p className="text-sm text-right italic">{date}</p>
      <section dangerouslySetInnerHTML={{ __html: post.html }} />
      <SocialShare title={postTitle} author={author} />
      {!!utterances && <Utterances repo={utterances} />}
    </main>
  )
}

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
        comment {
          utterances
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 280)
      html
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
      }
    }
  }
`

export default PostTemplate
